.container {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid var(--Cream);
  margin-bottom: 1.25rem;
}
.container h4 {
  text-align: center;
  margin-bottom: 1.25rem;
} 

.iframeContainer{
  position: relative;
  width: 100%;
  padding-bottom: 56.25%; 
  height: 0;
}
.iframeContainer iframe{
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  
}

